"use client";
import { useState, useEffect } from "react";
import Header from "../../components/Header";
import {
	Breadcrumb,
	BreadcrumbItem,
	BreadcrumbLink,
	BreadcrumbList,
	BreadcrumbPage,
	BreadcrumbSeparator,
} from "@/components/ui/breadcrumb";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import { Counter } from "@/components/counter";
import { StarRating, RatingForm, RatingList } from "@/components/ratings/index";
import { api } from "@/services/api";
import { ProviderType } from "@/types/types";

export default function ProductDetailsPage({
	params,
}: {
	params: { id: string };
}) {
	const [product, setProduct] = useState({
		id: params.id,
		name: "Nome do Produto",
		price: 260,
		description: "Descrição do produto",
		provider: "BRASILIAN" as ProviderType,
		averageRating: 4.2,
		ratingCount: 15,
	});
	const [userRating, setUserRating] = useState<number | null>(null);
	const [ratings, setRatings] = useState<any[]>([]);

	// Busca os dados do produto e avaliações
	useEffect(() => {
		const fetchData = async () => {
			try {
				// Substitua por sua chamada real à API
				const productData = await api.products.getById(
					product.provider,
					product.id,
					"user-token" // Adicione lógica de autenticação
				);
				setProduct(productData);

				const ratingsData = await api.ratings.getProductRatings(
					product.id,
					product.provider
				);
				setRatings(ratingsData);
			} catch (error) {
				console.error("Erro ao carregar dados:", error);
			}
		};

		fetchData();
	}, [product.id, product.provider]);

	return (
		<main className="flex flex-col min-h-screen">
			<Header />

			{/* Breadcrumbs */}
			<section className="px-4 py-2">
				<Breadcrumb>
					<BreadcrumbList>
						<BreadcrumbItem>
							<BreadcrumbLink href="/">Home</BreadcrumbLink>
						</BreadcrumbItem>
						<BreadcrumbSeparator />
						<BreadcrumbItem>
							<BreadcrumbLink href="/products">Produtos</BreadcrumbLink>
						</BreadcrumbItem>
						<BreadcrumbSeparator />
						<BreadcrumbItem>
							<BreadcrumbPage>{product.name}</BreadcrumbPage>
						</BreadcrumbItem>
					</BreadcrumbList>
				</Breadcrumb>
			</section>

			{/* Conteúdo Principal */}
			<section className="flex-1 px-4 pb-20">
				{/* Imagem do Produto */}
				<div className="relative w-full h-64 mb-4 bg-gray-100 rounded-lg">
					<Image
						src="/placeholder-product.jpg"
						alt={product.name}
						fill
						className="object-contain"
						priority
					/>
				</div>

				{/* Informações do Produto */}
				<div className="mb-6">
					<h1 className="text-2xl font-bold mb-1">{product.name}</h1>

					{/* Ratings - Média */}
					<div className="flex items-center mb-2">
						<StarRating rating={product.averageRating} size="sm" />
						<span className="ml-2 text-sm text-gray-600">
							({product.ratingCount} avaliações)
						</span>
					</div>

					<h2 className="text-xl font-semibold mb-3">
						${product.price.toFixed(2)}
					</h2>
					<p className="text-gray-700 mb-4">{product.description}</p>

					<div className="h-px bg-gray-200 my-4"></div>
				</div>

				{/* Contador e Botão */}
				<div className="flex items-center gap-3 mb-8">
					<Counter />
					<Button className="flex-1">Adicionar ao carrinho</Button>
				</div>

				{/* Seção de Avaliações */}
				<div className="mt-8">
					<h3 className="text-lg font-semibold mb-4">Avaliações</h3>

					{/* Formulário de Avaliação */}
					<RatingForm
						productId={product.id}
						provider={product.provider}
						userRating={userRating}
					/>

					{/* Lista de Avaliações */}
					<RatingList ratings={ratings} />
				</div>
			</section>
		</main>
	);
}
